html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
   
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
}

// p {
//     color: $negro;
//     font-size: 1.6rem;
//     //text-align: justify;
// }

.contenedor {
    width: 95%;
    max-width: 120rem;
    height: auto;
    margin: 0 auto;
    padding: 0;
}
.seccion{
    max-width: 80rem ;
}
a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1,
h2,
h3,
h4,
a {
    font-family: $fuente_principal;
    margin: calc($separacion / 2) 0;
    font-weight: bold;
    text-align: center;
}

h1 {
    font-size: 3.8rem;    
}

h2 {
    font-size: 3.4rem;
}

h3 {
    font-size: 3rem;
}

h4 {
    font-size: 2.2rem;
}
label{
    margin-left: 2rem;
}